.shining_button {
    margin: 1rem;
    font-size: 1.5rem;
    padding: 1rem 3rem;
    color: rgba(245, 59, 59, 0.995);
    background: transparent;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-decoration: none;
    border: 1px solid rgba(150, 150, 150, 0.5);
    position: relative;
    overflow: hidden;
    cursor: pointer;
    transition: font-size 0.5s ease-in-out, padding 0.5s ease-in-out,
      letter-spacing 0.5s ease-in-out;
  }
  
  .shining_button:disabled {
    color: rgba(150, 150, 150, 0.1);
    border: 1px solid rgba(150, 150, 150, 0.1);
  }
  
  .shining_button:not(:disabled):hover {
    color: rgba(150, 150, 150, 0.8);
    box-shadow: 1px 1px 20px 1px rgba(150, 150, 150, 0.5);
  }
  
  .shining_button:not(:disabled):before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      120deg,
      transparent,
      rgba(187, 187, 187, 0.1),
      transparent
    );
    transition: all 650ms;
  }
  
  .shining_button:not(:disabled):hover:before {
    left: 100%;
  }